/* stylelint-disable */
@import "./disable-mob-hover";

@mixin btn {
  font-size: 24px;

  padding: 20px 40px;

  color: var(--light-color);
  background-color: var(--black-08);
  border: 2px solid var(--light-color);
  border-radius: 8px;
  box-shadow: 0 0 15px var(--main-blue-05);

  @include tablet {
    padding: 10px 20px;
    font-size: 18px;
  }
}

@mixin transition {
  transition: background-color .5s ease-in-out,
  opacity 0.15s ease-in-out,
  transform .15s ease-in-out,
  box-shadow .15s ease-in-out;
}

@mixin hover-active-btn {
  &:focus {
    background-color: var(--main-red);
    transform: translateY(-5%) scale(1.05);
    box-shadow: 0 5px 15px var(--main-red);
  }

  @include hover {
    background-color: var(--main-red);
    transform: translateY(-5%) scale(1.05);
    box-shadow: 0 5px 15px var(--main-red);
  }

  &:active {
    background-color: var(--main-red-05);
    transform: none;
    box-shadow: none;
  }
}
