// если вы хотите использовать sass-переменные - удалите root
// colors
@import "./mixins";

:root {
  // base
  --font-family: "SawarabiGothic", sans-serif;
  --content-width: 1770px;
  --container-offset: 75px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // sizes
  --gallery-bg-size: 600px;
  --swiper-height: 600px;

  // colors
  --light-color: #fff;
  --main-blue: #293ce5;
  --main-blue-05: rgba(41, 60, 229, 0.5);
  --main-red: #8a0808;
  --main-red-05: rgba(138, 8, 8, 0.5);;
  --black-08: rgba(0, 0, 0, 0.8);
  --swiper-theme-color: #fff;

  @include big-desktop {
    --container-offset: 35px;
  }

  @include tablet {
    --swiper-navigation-size: 22px;
  }

  @include mobile {
    --container-offset: 15px;
  }
}
