@import "../mixins";

.footer {
  background-color: var(--black-08);
  padding-bottom: calc(var(--container-offset) / 2);
  color: var(--light-color);
  font-size: 22px;

  @include big-desktop {
    font-size: 18px;
  }

  @include mobile {
    font-size: 12px;
  }

  &__list {
    @include flex;

    justify-content: space-between;

    @include tablet {
      flex-direction: column;
    }

    @include mobile {
      gap: 10px;
    }
  }

  &__docs {
    color: var(--light-color);
    border-bottom: 2px solid transparent;
    transition: color .15s ease-in-out, border-color .15s ease-in-out;

    @include hover {
      color: var(--main-blue);
      border-color: var(--main-blue);
    }
  }

  &__docs:not(:last-child) {
    margin-right: 50px;

    @include mobile {
      margin-right: 0;
    }
  }
}
