@import '../mixins';

.about-us {
  color: var(--light-color);
  padding-block: var(--container-offset);
  background-color: var(--black-08);

  &__list {
    @include flex;
    flex-wrap: wrap;
    column-gap: var(--container-offset);
    row-gap: 30px;
    margin: 0 auto;
  }

  &__item {
    padding: 10px;
    width: (calc((100% - var(--container-offset) * 2) / 3));
    border: 2px solid var(--light-color);
    border-radius: 8px;
    transition: transform 1s ease-in-out;
    transform: translateX(-500%);
    box-shadow: 0 0 10px var(--main-blue-05);

    &--active {
      transform: none;
    }

    @include tablet {
      width: calc((100% - var(--container-offset)) / 2);
    }

    @include mobile {
      width: 100%;
    }
  }

  &__img {
    width: 64px;
    height: 64px;
    margin-bottom: 10px;
    filter: drop-shadow(0 0 3px var(--main-blue-05));
    transition: filter .15s ease-in-out;
  }

  &__h3 {
    margin: 0;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 15px;
  }

  &__descr {
    margin: 0;
    font-size: 18px;
  }
}
