@import '../mixins';

.header {
  min-width: 100%;

  position: fixed;
  z-index: 100;

  .section-bg {
    padding-block: 10px;
  }

  &__wrapper {
    @include flex;
    justify-content: space-between;
    align-items: center;

    @include big-desktop {
      position: relative;
    }
  }

  &__nav {
    @include flex-v-center;

    @include big-desktop {
      position: absolute;
      top: 140%;
      right: 0;

      &--active {
        .nav__list {
          transform: scaleY(1);
          padding: 20px;
          border: 2px solid #333333;
          border-radius: 8px;
          background-color: #333333;
          box-shadow: 0 0 15px var(--main-blue-05);

          --gap: 20px;
        }
      }
    }
  }

  &__burger {
    position: relative;
    display: none;
    width: 30px;
    height: 30px;

    @include big-desktop {
      display: block;
    }

    .burger__line {
      position: absolute;
      left: 0;
      width: 30px;
      height: 2px;
      background-color: var(--light-color);
      transform-origin: left;
      transition: transform .15s ease-in-out, top .15s ease-in-out, scale .15s ease-in-out, left .15s ease-in-out;


      &:first-child {
        top: 0;
      }

      &:nth-child(2) {
        top: 50%;
        transform: translateY(-50%);
      }

      &:last-child {
        bottom: 0;
      }
    }

    &--active {
      .burger__line {
        left: 4px;
      }

      .burger__line:first-child {
        top: 4px;
        transform: rotate(45deg);
      }

      .burger__line:last-child {
        bottom: 3px;
        transform: rotate(-45deg);
      }

      .burger__line:nth-child(2) {
        scale: 0;
      }
    }
  }
}

.header .section-bg {
  background-color: #333333;
}

.nav {
  &__list {
    @include flex;
    --gap: 50px;
    gap: var(--gap);

    @include big-desktop {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      transition: all .15s ease-in-out;
      transform: scaleY(0);
      transform-origin: top;
    }

    &--item {
      padding: 10px;
      color: var(--black-08);
      font-size: 20px;
      border: 2px solid var(--light-color);
      border-radius: 8px;
      background-color: var(--light-color);
      transition: background-color .15s ease-in-out, border-color .15s ease-in-out;

      .anchor {
        display: block;
      }

      &:nth-child(1) {
        position: relative;

        @include hover {
          .phones--wrapper {
            transform: scaleY(1);
          }
        }
      }

      &:nth-child(3) {
        position: relative;
        margin-right: var(--gap);

        &::after {
          position: absolute;
          content: "";
          width: 2px;
          height: 100%;
          top: 0;
          right: calc(var(--gap) * -1);
          transform: translateX(150%);
          background-color: var(--light-color);
        }
      }

      &:focus {
        background-color: var(--main-red);
        border-color:var(--light-color);
        color: var(--light-color);
      }

      @include hover {
        background-color: var(--main-red);
        border-color:var(--light-color);
        color: var(--light-color);
      }


      &:active{
        color: var(--light-color);
        background-color: var(--main-red-05);
      }

      @include big-desktop {
        width: 100%;

        &:nth-child(3) {
          margin-right: 0;
          margin-bottom: var(--gap);

          &::after {
            display: none;
          }

          &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 2px;
            left: 0;
            bottom: calc(var(--gap) * -1);
            background-color: var(--light-color);
            transform: translateY(200%);
          }
        }
      }

      @include mobile {
      font-size: 18px;
      }
    }
  }


}

.phones {
  @include flex;
  flex-direction: column;
  border-radius: 8px;
  background-color: var(--black-08);
  padding: 10px;
  color: var(--light-color);

  &--wrapper {
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 70px;
    transform: scaleY(0);

    @include hover {
      .phones-wrapper {
        transform: scaleY(1);
      }
    }

    @include big-desktop {
      padding-top: 0;
      padding-right: 110%;
    }

    &--active {
      transform: scaleY(1);
    }
  }

  &--link {
    white-space: nowrap;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &:focus {
      .phones {
        transform: scaleY(1);
      }
    }

    @include hover {
      text-decoration: underline
    }
  }
}

