@import '../mixins';

.to-start {
  @include flex-all-center;
  position: fixed;
  z-index: 2;

  right: 30px;
  bottom: 60px;

  opacity: 1;
  width: 96px;
  height: 96px;

  box-shadow: 0 0 15px var(--main-blue-05);
  border: 1px solid var(--light-color);
  border-radius: 50%;
  background-color: #333;

  @include small-tablet {
    width: 48px;
    height: 48px;
  }
}
