@import '../mixins';

.contact-us {
  padding-block: var(--container-offset);
  background-color: var(--black-08);

  &__wrapper {
    @include flex;
    gap: var(--container-offset);

    @include tablet {
      flex-direction: column;
    }
  }

  &__map {
    width: calc((100% - var(--container-offset) - 33%));
    min-height: 100%;
    // background-color: black;
    border: 1px solid var(--light-color);
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 25px var(--main-blue-05);

    @include tablet {
      width: 100%;
      min-height: calc((100dvw - var(--container-offset) * 2) / 2);
    }

    @include mobile {
      min-height: calc(100dvw - var(--container-offset) * 2);
    }
  }
}

.form {
  @include flex;
  flex-direction: column;
  align-items: start;
  gap: 40px;
  padding: 25px;

  width: 33%;
  min-width: 500px;

  border: 2px solid var(--light-color);
  border-radius: 8px;
  box-shadow: 0 0 15px var(--main-blue-05);

  @include tablet {
    min-width: 100%;
    gap: 20px;
    padding: 15px;
  }

  @include mobile {
    padding: 10px;
    gap: 15px;
  }

  label {
    position: relative;
    width: 100%;
  }

  &__input {
    padding: 20px 20px;

    width: 100%;

    border: 2px solid var(--light-color);
    border-radius: 8px;
    box-shadow: 0 0 15px var(--main-blue-05);

    background-color: transparent;
    color: var(--light-color);

    &::placeholder {
      color: var(--light-color);
    }

    &:focus {
      outline: none;
      background-color: rgba($color: #293ce5, $alpha: .25);
    }

    @include mobile {
      padding: 10px 10px;
    }
  }

  &__btn {
    padding: 20px 20px;

    width: 40%;

    background-color: transparent;
    color: var(--light-color);

    border: 2px solid var(--light-color);
    border-radius: 8px;
    box-shadow: 0 0 15px var(--main-blue-05);

    transition: background-color .15s ease-in-out;

    &:focus {
      background-color: var(--main-blue);
      outline: none;
    }

    @include hover {
      background-color: var(--main-blue);
    }

    @include mobile {
      width: 100%;
      padding: 10px 10px;
    }
  }

  &__descr {
    font-size: 12px;
    margin: 0;
    color: var(--light-color);
  }

  &__docs {
    cursor: pointer;
    word-wrap: balance;
    text-decoration: underline;
    color: var(--light-color);
  }
}

.just-validate-error-label[data-tooltip=true] {
  background: var(--main-red) !important;

  position: absolute !important;
  top: 80% !important;
  left: 0 !important;

  @include mobile {
    top: 65% !important;
  }
}

.just-validate-error-label[data-tooltip=true]:before {
  border-bottom: 5px solid var(--main-red) !important;
}

.invalid {
  background-color: rgba($color: #8a0808, $alpha: .25) !important;
  box-shadow: 0 0 15px var(--main-red-05);

  margin-bottom: 30px;
}

[class*="copyrights-pane"] {
  display: none !important;
}

[class*="ground-pane"] {
  filter: grayscale(1);
}
