@import '../mixins';

.hero {
  height: 100dvh;

  &__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  &__h2 {
    margin: 0;
    font-size: 56px;
    text-align: center;

    @include tablet {
      font-size: 40px;
    }

    @include mobile {
      font-size: 28px;
    }
  }

    &__logo {
      max-width: 500px;
      width: 100%;

      @media (max-width: 768px) and (min-width: 577px) and (max-height: 576px) {
        width: 200px;
      }
    }

  &__descr {
    margin: 0;
    margin-bottom: 45px;

    font-size: 22px;
    line-height: 1.5;
    font-weight: 600;

    align-self: flex-start;

    @include big-desktop {
      display: none
    }
  }

  &__btn {
    @include btn;
    @include transition;
    @include hover-active-btn;

    border: none;

    min-width: min-content;
    white-space: nowrap;

    @include big-desktop {
      width: 100%;
    }
  }
}

.hero__wrapper {
  @include flex;
  justify-content: space-evenly;

  min-height: 470px;

  gap: calc(var(--container-offset) / 2);

  @include big-desktop {
    flex-direction: column;
    align-items: center;

    min-height: min-content;
    width: calc(100dvw - var(--container-offset) * 4);
    max-width: 40%;
    margin: 0 auto;
    gap: var(--container-offset);

  }

  @include small-tablet {
    max-width: calc(100dvw - var(--container-offset) * 4);
  }

  @media (max-width: 768px) and (min-width: 577px) and (max-height: 576px) {
    flex-direction: row;
  }

  .content-wrapper {
    max-width: calc(100% - 500px - var(--container-offset) * 2);
    @include flex;
    flex-direction: column;
    align-items: flex-start;

    @include big-desktop {
      max-width: none;
      width: 100%;
    }
  }
}

