@import '../mixins';

.gallery {
  padding-block: var(--container-offset);

  &__wrapper {
    @include flex;
    --inner-offset: 20px;

    gap: var(--inner-offset);
    padding: var(--inner-offset);

    border: 2px solid var(--black-08);
    border-radius: 8px;
    box-shadow: 0 0 15px var(--main-blue-05);
    background-color: var(--black-08);

    @include big-desktop {
      --inner-offset: 10px !important;
    }

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__list {
    @include flex;
    flex-direction: column;
    justify-content: space-between;

    @include small-tablet {
      gap: 10px;
      flex-direction: row;
      overflow-x: scroll;
      padding-bottom: 10px;
    }
  }

  &__item {
    font-size: 34px;
    font-weight: 600;
  }

  &__btn {
    @include btn;
    padding: var(--inner-offset);
    background-color: transparent;
    white-space: nowrap;

    &--active {
      background-color: var(--main-red);
      box-shadow: 0 5px 15px var(--main-red);
    }

    @include transition;
    @include hover-active-btn;

    @include big-desktop {
      font-size: 18px;
      // padding: 20px;
    }

    @include tablet {
      padding: 10px;
    }
  }

  &__content {
    overflow: hidden;
    width: 100%;
    box-shadow: 0 0 15px var(--main-blue-05);
    border: 2px solid var(--light-color);
    border-radius: 8px;
    background-color: var(--black-08);

    .swiper {
      display: none;
      height: calc((100dvw - var(--container-offset) * 10) * 0.6);
      max-height: calc(100dvh - var(--container-offset) * 2);
      width: 100%;

      &--active {
        display: block;
      }

      &-slide {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
      }

      @include small-tablet {
        height: 300px;
      }

      @include mobile {
        height: calc((100dvw - var(--container-offset) * 2) * 0.6);
      }
    }
  }
}

.swiper-button-next,
.swiper-button-prev {
  width: calc(var(--swiper-navigation-size) / 2);

  &::after {
    border: 2px solid var(--light-color);
    box-shadow: 0 0 15px var(--main-blue-05);
    background-color: var(--main-blue-05);
    padding: 100%;
    transition: background-color .15s ease-in-out;
  }

  @include hover {
    &::after {
      background-color: var(--main-blue);
    }
  }
}

.swiper-button-next::after {
  border-radius: 50% 0 0 50%;
}

.swiper-button-prev::after {
  border-radius: 0 50% 50% 0;
}

